import type { NumberFieldValue } from '@/store/statusData';
import type { Field } from '@/type/settings';
import { logger } from '@/utils/logger';
import { eventSource, event_types } from '@sillytavern/script';
import { getContext } from '@sillytavern/scripts/extensions';
import { parse as parseYAML } from 'yaml';

/**
 *  ```myst 
 * @param messageText 
 * @returns  null
 */
export function parseMinguCodeBlock(messageText: string): Record<string, any> | null {
  if (!messageText) return null;

  //  ```myst 
  const codeBlockRegex = /```myst\s*\n([\s\S]*?)```/;
  const match = messageText.match(codeBlockRegex);

  if (!match) return null;

  const codeContent = match[1].trim();

  try {
    //  YAML
    const data = parseYAML(codeContent);
    logger.log('[messageParser]  myst ');
    return data;
  } catch (error) {
    logger.error('[messageParser]  myst :', error);
    return null;
  }
}

/**
 * 
 * @param rawData  YAML 
 * @param fields 
 * @returns 
 */
export function processStatusData(
  rawData: Record<string, any>,
  fields: Field[],
): {
  date: string;
  location: string;
  weather: string;
  news: { title: string; content: string };
  customFields: Record<string, string | number | NumberFieldValue>;
} {
  //  key
  const fixedData = {
    date: rawData['time'] || rawData[''] || rawData['date'] || '',
    location: rawData['place'] || rawData[''] || rawData['location'] || '',
    weather: rawData['weather'] || rawData[''] || '',
    news: {
      title: rawData['news']?.['title'] || rawData['']?.[''] || '',
      content: rawData['news']?.['content'] || rawData['']?.[''] || '',
    },
  };

  // 
  const customFields: Record<string, string | number | NumberFieldValue> = {};

  for (const field of fields) {
    if (!field.enabled) continue; // 

    const fieldName = field.name;
    if (fieldName && rawData[fieldName] !== undefined) {
      // 
      if (field.type === 'number') {
        const rawValue = String(rawData[fieldName]).trim();
        const cleanValue = rawValue.replace(/%$/, ''); //  % 
        const numValue = Number(cleanValue);

        // 
        if (!isNaN(numValue)) {
          customFields[field.id] = {
            value: numValue, // 
            display: rawValue, //  %
          };
        } else {
          customFields[field.id] = rawData[fieldName];
        }
      } else {
        customFields[field.id] = String(rawData[fieldName]);
      }
    }
  }

  return {
    ...fixedData,
    customFields,
  };
}

/**
 * 
 * 
 * @param fields 
 * @returns  null
 */
export function parseLatestMessage(fields: Field[]) {
  const context = getContext();
  if (!context.chat || context.chat.length === 0) {
    return null;
  }

  //  AI 
  for (let i = context.chat.length - 1; i >= 0; i--) {
    const message = context.chat[i];

    // 
    if (message.is_user || message.is_system) {
      continue;
    }

    //  swipe 
    const messageText = message.swipes?.[message.swipe_id] || message.mes;

    // 
    const rawData = parseMinguCodeBlock(messageText);
    if (rawData) {
      // 
      logger.log(`[messageParser]  #${i} `);
      return processStatusData(rawData, fields);
    }
  }

  // 
  logger.log('[messageParser] ');
  return null;
}

/**
 * 
 * @param onStatusUpdate 
 * @param onStatusClear 
 * @param getFields 
 */
export function initMessageListener(
  onStatusUpdate: (data: ReturnType<typeof processStatusData>) => void,
  onStatusClear: () => void,
  getFields: () => Field[],
) {
  logger.log('[messageParser] ');

  // 
  const handleMessageUpdate = () => {
    const fields = getFields();
    const data = parseLatestMessage(fields);
    if (data) {
      onStatusUpdate(data);
    } else {
      onStatusClear();
    }
  };

  //  AI 
  eventSource.on(event_types.MESSAGE_RECEIVED, () => {
    logger.log('[messageParser]  AI ');
    handleMessageUpdate();
  });

  //  swipe
  eventSource.on(event_types.MESSAGE_SWIPED, () => {
    logger.log('[messageParser]  swipe');
    handleMessageUpdate();
  });

  // 
  eventSource.on(event_types.MESSAGE_EDITED, () => {
    logger.log('[messageParser] ');
    handleMessageUpdate();
  });

  // 
  eventSource.on(event_types.CHAT_CHANGED, () => {
    logger.log('[messageParser] ');
    handleMessageUpdate();
  });

  // 
  eventSource.on(event_types.MESSAGE_DELETED, () => {
    logger.log('[messageParser] ');
    handleMessageUpdate();
  });

  // 
  handleMessageUpdate();
}
