import type { Field, FixedFieldsEnabled } from '@/type/settings';
import { logger } from '@/utils/logger';
import {
  eventSource,
  event_types,
  extension_prompt_roles,
  extension_prompt_types,
  setExtensionPrompt,
} from '@sillytavern/script';

/**
 *  prompt 
 */
export const DEFAULT_PROMPT_ZH_TW = {
  time: ',  yyyyMMddHHmm',
  place: ',  123',
  weather: ',  ',
  newsTitle:
    ', , , , ',
  newsContent: ',, 100',
};

/**
 *  prompt 
 */
export const DEFAULT_PROMPT_ZH_CN = {
  time: ',  yyyyMMddHHmm',
  place: ',  123',
  weather: ',  ',
  newsTitle:
    ', , , , ',
  newsContent: ',, 100',
};

/**
 *  prompt 
 */
export const DEFAULT_PROMPT_EN = {
  time: 'Current time, format: yyyy-MM-dd, Weekday, HH:mm',
  place: 'Current story location, format: Location Layer 1 . Location Layer 2 . Location Layer 3',
  weather: 'Current weather or environment temperature, format: Elegant description of light . Temperature . Weather',
  newsTitle:
    'Current event headline in society. Can be science news, viral forum posts, must be related to the plot or characters. A bit humorous and funny, must be different type from previous round, each round must have different type, use your imagination',
  newsContent:
    "The content of this event, if it's news then broadcast in news tone, if it's forum then use netizen internet speech tone, and so on. Within 100 words",
};

/**
 *  prompt  prompt 
 */
export function buildFixedPrompt(
  language: 'zh-TW' | 'zh-CN' | 'en',
  fixedFieldsEnabled: FixedFieldsEnabled,
  customPrompt?: {
    time?: string;
    place?: string;
    weather?: string;
    newsTitle?: string;
    newsContent?: string;
  },
): string {
  const defaultPrompt =
    language === 'en' ? DEFAULT_PROMPT_EN : language === 'zh-CN' ? DEFAULT_PROMPT_ZH_CN : DEFAULT_PROMPT_ZH_TW;
  const prompt = customPrompt || defaultPrompt;

  const header =
    language === 'en'
      ? 'Status Bar:\nAfter all text, you must generate the following code block status bar:\n**Must be different from the previous round and output in English**'
      : language === 'zh-CN'
        ? ':\n  ():\n**, **'
        : ':\n  ():\n**, **';

  // 
  const lines: string[] = [];

  if (fixedFieldsEnabled.time) {
    lines.push(`time: \${${prompt.time || defaultPrompt.time}}`);
  }
  if (fixedFieldsEnabled.place) {
    lines.push(`place: \${${prompt.place || defaultPrompt.place}}`);
  }
  if (fixedFieldsEnabled.weather) {
    lines.push(`weather: \${${prompt.weather || defaultPrompt.weather}}`);
  }
  if (fixedFieldsEnabled.news) {
    lines.push(
      `news:\n  title: \${${prompt.newsTitle || defaultPrompt.newsTitle}}\n  content: \${${prompt.newsContent || defaultPrompt.newsContent}}`,
    );
  }

  return `${header}
\`\`\`myst
${lines.join('\n')}`;
}

/**
 *  prompt
 * @param fields 
 * @param language 
 * @param fixedFieldsEnabled 
 * @param customPrompt  prompt
 * @returns  prompt 
 */
export function generateStatusPrompt(
  fields: Field[],
  language: 'zh-TW' | 'zh-CN' | 'en' = 'zh-TW',
  fixedFieldsEnabled: FixedFieldsEnabled = {
    time: true,
    place: true,
    weather: true,
    news: true,
  },
  customPrompt?: {
    time?: string;
    place?: string;
    weather?: string;
    newsTitle?: string;
    newsContent?: string;
  },
): string {
  const fixedPrompt = buildFixedPrompt(language, fixedFieldsEnabled, customPrompt);

  //  prompt
  const dynamicPrompt = fields
    .filter(f => f.enabled)
    .map(f => {
      // 
      if (f.description && f.description.trim()) {
        return `${f.name}: \${${f.description}}`;
      } else {
        return `${f.name}: \${${f.name}}`;
      }
    })
    .join('\n');

  // 
  const fullPrompt = dynamicPrompt ? `${fixedPrompt}\n${dynamicPrompt}\n\`\`\`` : `${fixedPrompt}\n\`\`\``;

  return fullPrompt;
}

// 
let currentFields: Field[] = [];
let currentLanguage: 'zh-TW' | 'zh-CN' | 'en' = 'zh-TW';
let currentFixedFieldsEnabled: FixedFieldsEnabled = {
  time: true,
  place: true,
  weather: true,
  news: true,
};
let currentCustomPrompt:
  | {
      time?: string;
      place?: string;
      weather?: string;
      newsTitle?: string;
      newsContent?: string;
    }
  | undefined = undefined;
let isEnabled = false;

/**
 *  prompt 
 * @param fields 
 * @param language 
 * @param fixedFieldsEnabled 
 * @param customPrompt  prompt
 */
export function enablePromptInjection(
  fields: Field[],
  language: 'zh-TW' | 'zh-CN' | 'en' = 'zh-TW',
  fixedFieldsEnabled: FixedFieldsEnabled = {
    time: true,
    place: true,
    weather: true,
    news: true,
  },
  customPrompt?: {
    time?: string;
    place?: string;
    weather?: string;
    newsTitle?: string;
    newsContent?: string;
  },
): void {
  currentFields = fields;
  currentLanguage = language;
  currentFixedFieldsEnabled = fixedFieldsEnabled;
  currentCustomPrompt = customPrompt;
  isEnabled = true;
  logger.log('[promptGenerator]  Prompt ');
}

/**
 *  prompt 
 */
export function disablePromptInjection(): void {
  isEnabled = false;
  logger.log('[promptGenerator]  Prompt ');

  //  prompt
  setExtensionPrompt('status-tracking-prompt', '', extension_prompt_types.IN_CHAT, 0, false);
}

/**
 *  prompt GENERATION_STARTED 
 */
function onGenerationStarted(): void {
  if (!isEnabled) {
    setExtensionPrompt('status-tracking-prompt', '', extension_prompt_types.IN_CHAT, 0, false);
    return;
  }

  const prompt = generateStatusPrompt(currentFields, currentLanguage, currentFixedFieldsEnabled, currentCustomPrompt);
  logger.log('[promptGenerator]  Prompt ');

  //  prompt  0
  setExtensionPrompt(
    'status-tracking-prompt', //  key
    prompt, // prompt 
    extension_prompt_types.IN_CHAT, // 
    0, //  0
    false, //  world info 
    extension_prompt_roles.SYSTEM, // SYSTEM 
  );
}

/**
 *  prompt 
 */
export function initPromptInjection(): void {
  logger.log('[promptGenerator]  Prompt ');

  // 
  eventSource.on(event_types.GENERATION_STARTED, onGenerationStarted);
}
