// 
export type NumberFieldValue = {
  value: number; // 
  display: string; //  % 
};

export const useStatusDataStore = defineStore('statusData', () => {
  const data = ref({
    date: '',
    location: '',
    weather: '',
    news: {
      title: '',
      content: '',
    },
    customFields: {} as Record<string, number | string | NumberFieldValue>,
  });

  // 
  function clearData() {
    data.value = {
      date: '',
      location: '',
      weather: '',
      news: {
        title: '',
        content: '',
      },
      customFields: {},
    };
  }

  return { data, clearData };
});
