import { generateUUID } from '@/utils/uuid';

// 1.  type import
export type Field = {
  id: string;
  enabled: boolean;
  name: string;
  description: string;
  order: number;
  type: 'number' | 'text';
};

// 2. Field schema
const FieldSchema = z.object({
  id: z.string(),
  enabled: z.boolean(),
  name: z.string(),
  description: z.string(),
  order: z.number(),
  type: z.enum(['number', 'text']).default('number'),
});

// 3. Custom Prompt schema
const CustomPromptSchema = z.object({
  time: z.string().optional(),
  place: z.string().optional(),
  weather: z.string().optional(),
  newsTitle: z.string().optional(),
  newsContent: z.string().optional(),
});

// 4. Fixed Fields Enabled schema/
const FixedFieldsEnabledSchema = z.object({
  time: z.boolean(),
  place: z.boolean(),
  weather: z.boolean(),
  news: z.boolean(),
});

// 5. Settings schema
export const Settings = z
  .object({
    button_selected: z.boolean().default(false),
    panel_enabled: z.boolean().default(false),
    panel_position: z.enum(['left', 'right']).default('right'),
    panel_collapsed: z.boolean().default(false),
    fields: z.array(FieldSchema).default([
      // 
      {
        id: generateUUID(),
        enabled: true,
        name: '',
        description: '<char><user>, 0-100%, 0.5',
        order: 0,
        type: 'number' as const,
      },
      {
        id: generateUUID(),
        enabled: true,
        name: '',
        description: "<char>'', 100",
        order: 1,
        type: 'text' as const,
      },
    ]),
    language: z.enum(['zh-TW', 'zh-CN', 'en']).default('zh-TW'),
    progress_color_low: z.string().default('#d8b4a0'), // 
    progress_color_high: z.string().default('#a0b4d8'), // 
    debug_mode: z.boolean().default(false), // Debug 
    custom_prompt: CustomPromptSchema.optional(), //  prompt
    fixed_fields_enabled: FixedFieldsEnabledSchema.default({
      time: true,
      place: true,
      weather: true,
      news: true,
    }), // 
    custom_css: z.string().default(''), //  CSS
  })
  .prefault({});

// 6.  zod schema  TypeScript type
export type Settings = z.infer<typeof Settings>;
export type CustomPrompt = z.infer<typeof CustomPromptSchema>;
export type FixedFieldsEnabled = z.infer<typeof FixedFieldsEnabledSchema>;

// 7. 
export const setting_field = 'status_tracking';
