<template>
  <div class="field-item">
    <!--  handle -->
    <div class="drag-handle">
      <i class="fa-solid fa-grip-vertical"></i>
    </div>

    <!-- checkbox -->
    <input
      type="checkbox"
      :checked="field.enabled"
      @change="$emit('update:enabled', ($event.target as HTMLInputElement).checked)"
    />

    <!--  -->
    <input
      type="text"
      class="text_pole field_key"
      :value="field.name"
      :placeholder="t``"
      @input="$emit('update:name', ($event.target as HTMLInputElement).value)"
    />

    <!-- AI -->
    <input
      type="text"
      class="text_pole field_value"
      :value="field.description"
      :placeholder="t`AI`"
      @input="$emit('update:description', ($event.target as HTMLInputElement).value)"
    />
    <!--  -->
    <label>
      <input type="radio" :checked="field.type === 'number'" @change="$emit('update:type', 'number')" />
      {{ t`` }}
    </label>
    <label>
      <input type="radio" :checked="field.type === 'text'" @change="$emit('update:type', 'text')" />
      {{ t`` }}
    </label>
    <!--  -->
    <div class="menu_button" @click="$emit('delete')">
      <i class="fa-solid fa-trash"></i>
    </div>
  </div>
</template>

<script setup lang="ts">
import { useI18nStore } from '@/store/i18n';
import type { Field } from '@/type/settings';

const { t } = useI18nStore();

// Props
defineProps<{
  field: Field;
}>();

// Emits
defineEmits<{
  'update:enabled': [value: boolean];
  'update:name': [value: string];
  'update:description': [value: string];
  'update:type': [value: 'number' | 'text'];
  delete: [];
}>();
</script>
